/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

/* Industry section styles for elegance and readability */
.industry-section {
  background: var(--color-bg-secondary);
  border-radius: 1rem;
  margin: 2rem auto;
  padding: 3rem 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  max-width: 900px;
}

.industry-container {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.industry-card {
  background: var(--color-bg-primary);
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  margin-bottom: 2.5rem;
  padding: 2rem 1.5rem;
  text-align: left;
  transition: box-shadow 0.2s;
}
.industry-card:hover {
  box-shadow: 0 4px 16px rgba(0,123,255,0.10);
}

.industry-role {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--color-accent-primary);
  margin-bottom: 0.5rem;
}

.industry-period {
  font-style: italic;
  color: var(--color-accent-secondary);
}

.industry-details {
  margin-top: 1rem;
  margin-bottom: 0;
  padding-left: 1.5rem;
}

.industry-detail {
  margin-bottom: 0.5rem;
  color: var(--color-text-neutral);
  font-size: 1rem;
  line-height: 1.7;
}

/* Education section styles for elegance and readability */
.education-section {
  background: var(--color-bg-secondary);
  border-radius: 1rem;
  margin: 2rem auto;
  padding: 3rem 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  max-width: 900px;
}

.education-card {
  background: var(--color-bg-primary);
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  margin-bottom: 2.5rem;
  padding: 2rem 1.5rem;
  text-align: left;
  transition: box-shadow 0.2s;
}
.education-card:hover {
  box-shadow: 0 4px 16px rgba(23,162,184,0.10);
}

.education-degree {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--color-accent-secondary);
  margin-bottom: 0.5rem;
}

.education-period {
  font-style: italic;
  color: var(--color-accent-primary);
}

.education-details {
  margin-top: 1rem;
  margin-bottom: 0;
  padding-left: 1.5rem;
}

.education-detail {
  margin-bottom: 0.5rem;
  color: var(--color-text-neutral);
  font-size: 1rem;
  line-height: 1.7;
}

/* Header styles for elegance and readability */
.header-bar {
  background: var(--color-bg-dark) !important;
  color: var(--color-text-light);
  position: fixed;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1.2rem 0;
  top: 0;
  width: 100%;
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}
.header-bar a {
  color: var(--color-text-light);
  font-weight: 500;
  font-size: 1.1rem;
  letter-spacing: 0.02em;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  transition: background 0.2s, color 0.2s;
}
.header-bar a:hover {
  background: var(--color-accent-primary);
  color: var(--color-text-white);
}

/* Footer styles for elegance and readability */
.footer-bar {
  background: var(--color-bg-dark);
  color: var(--color-text-light);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  padding: 3rem 0 2rem;
  width: 100vw;
  box-shadow: 0 -2px 8px rgba(0,0,0,0.08);
}
.footer-bar .socialIcon {
  height: 32px;
  width: 32px;
  margin: 0 0.5rem;
  filter: grayscale(10%) brightness(1.2);
  transition: filter 0.2s;
}
.footer-bar .socialIcon:hover {
  filter: none;
}
.footer-bar p.small {
  margin-top: 0;
  color: var(--color-text-light);
  font-size: 1rem;
  letter-spacing: 0.01em;
}
